* {
    box-sizing: border-box;
   
    font-size: 3vh;
}

body {
    margin: 0;
    font-family: Inter, sans-serif;
    height: 100vh;
    background-color: #282D35;
}

nav {
    display: flex;
    align-items: center;
    background-color: #21222A;
    height: 10vh;
    padding: 30px 25px;
}

.nav--icon{
    width: 10vh;
}
.nav--logo_text, .nav--title {
    margin: 0;
}

.nav--logo_text {
    margin-right: auto;
    color: #61DAFB;
    font-weight: 700;
    font-size: 3vh;
}

.nav--title {
    color: #DEEBF8;
    font-weight: 600;
}

.nav-img {
    height: 30px;
    margin-right: 7px;
    width: 30;
 
}

main {
    padding: 57px 27px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
    background-image: url(../image/cc.png);
    background-repeat: no-repeat;
    background-position-x: right;
    background-position-y: center;
    background-size: 15%;
}

.main--title {
    margin: 0;
    font-size: 5vh;
    letter-spacing: -0.05em;
}

.main--facts {

    margin-top: 46px;
   
   min-width: 30%;
}

.main--facts > li {
    line-height: auto;
    padding-block: 10px;
}

.main--facts > li::marker {
    font-size: 1.4rem;
    color: #61DAFB;
}
footer{
    position: static;
    bottom: 0;
    height: 50px;
    width: 100%;
    height: 10vh;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bolder;
}
.div{
    display: flex;
}
.light{
    background-color: lightgray;
    color: #282D35;
}
.light>.nav--logo_text{
    color: #282D35;
}
.light>.nav--title{
    color: #282D35; 
}
.flight{
    background-color: rgb(87, 87, 87);
    color: white;
}
.mlight{
    background-color: white;
}
.mlight>.main--title{
    color: #282D35;
    font-weight: bold;
}
.mlight>.main--facts {
color: #282D35;
    margin-top: 46px;
    font-weight: bold;
   
   min-width: 30%;
}

.mlight>.main--facts > li::marker {
    font-size: 1.4rem;
    color: #38889e;
}
.toggle{
    width:4%;
margin: none;
 
}
.nav--title1{
   filter: brightness(60%);
}
.nav--title2{
 color: rgb(179, 179, 179);
 }
img.toggle {
    margin: 1%;
}
@media (width<900px) {
    .toggle{
        width:5%;
     margin-right: 2%;

    }
    
}
@media (width>1300px) {
    .toggle{
        width:2.5%;
     margin-right: 2%;

    }
    
}